<template>
    <div class="prompt" >
         <div class="content" ref="con" >
           <slot></slot>
            <p class="con" v-html="content"></p>
            <div class="btns">
              <p class="btn btn-left" @click="handleBtnLeft">{{btnLeftText}}</p>
              <p class="btn btn-right" @click="handleBtnRight">{{btnRightText}}</p>
            </div>
         </div>
         <div class="cover" @click="handleBtnLeft"></div>
    </div>
</template>

<script>
export default {
  name: 'mPrompt',
  props: {
    content: {
      type: String,
      default: "'"
    },
    btnLeftText: {
      type: String,
      default: "'"
    },
    btnRightText: {
      type: String,
      default: "'"
    }
  },
  methods: {
    handleBtnLeft () {
      this.$emit('handleBtnLeft')
    },
    handleBtnRight () {
      this.$emit('handleBtnRight')
    }
  }
}
</script>

<style scoped lang="scss">
  @import '@/assets/styles/mixin.scss';
  .prompt{
    position: fixed;
    top:0;
    bottom: 0;
    left: 0;
    top:0;
    right: 0;
    width: 100%;
    height:100%;
    z-index: 201;
    .content{
      position: absolute;
      top:pxToRem(420px);
      left:pxToRem(90px);
      background: #fff;
      width:pxToRem(570px);
      border-radius: pxToRem(12px);
      z-index: 104;
      text-align: center;
      .con{
        font-size: pxToRem(30px);
        color: $color-text;
        text-align: center;
        height:pxToRem(196px);
        line-height:  pxToRem(48px);
        padding:0 pxToRem(30px) ;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .btns{
        display: flex;
        justify-content: space-between;
        text-align: center;
        height: pxToRem(91px);
        line-height: pxToRem(91px);
        border-top: $border-bottom-light-color;
        font-size: pxToRem(30px);
        .btn{
          width:50%;
          font-size: pxToRem(30px);
        }
        .btn-right{
          /*color: #3875E2;*/
          color: $color-active-text;
          border-left:pxToRem(2px) solid #EDEDED;
        }
      }
    }
    .cover{
      position: fixed;
      bottom: 0;
      left: 0;
      top:0;
      right: 0;
      width: 100%;
      height:100%;
      z-index: 103;
      background: rgba(28,29,29,0.7);
    }
  }
</style>
