<template>
	<div class="settle" v-if="result">
		<div class="whiteBox">
			<div class="ttl">
				<div class="bd clearfix">
					理赔申请编号：{{result.sn}}
					<span class="fr">{{result.status | claimStatus}}</span>
				</div>
			</div>
			<div class="step">
				<div class="name">{{result.refer_name}}</div>
				<div class="step-bar" v-if="showBtn([0,1,2,3,4,6])">
					<div class="step-item" v-for="(step,i) in steps" :class="{active: stepNow==i+1,live:stepNow==i+1&&stepLive}">
						<div class="tl">{{step}}</div>
					</div>
					<div class="cover"></div>
				</div>
			</div>
			<div class="amount" v-if="showBtn([1,4,5])">
				初步理赔金额：{{result.initial_price || 0}}元
			</div>
			<div class="amount" v-if="showBtn([2,3])">
				理赔金额：{{result.amout || 0}}元
			</div>
		</div>
		<div class="whiteBox">
			<div class="paddingBox">
				<div class="name">申请信息</div>
				<div class="message bd">
					<div class="e-item">
						<div class="k">申请人</div>
						<div class="v">
							{{result.relation_name}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">被保险人</div>
						<div class="v">
							{{result.insurance_name}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">保险公司</div>
						<div class="v">
							{{result.brand_name}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">保障利益</div>
						<div class="v">
							{{result.item_config.formula}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">保单号</div>
						<div class="v">
							{{result.order_policy_num}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">订单编号</div>
						<div class="v">
							{{result.order_sn}}
						</div>
					</div>
					<div class="e-item">
						<div class="k">申请时间</div>
						<div class="v">
							{{result.create_time}}
						</div>
					</div>
					<div class="e-item" v-if="showBtn([3,5,6])">
						<div class="k">完结时间</div>
						<div class="v">
							{{result.occur_time}}
						</div>
					</div>

				</div>
				<div class="name">出险信息</div>
				<div class="message">
					<div class="e-item">
						<div class="k">出险时间</div>
						<div class="v">{{result.occur_time}}</div>
					</div>
					<div class="e-item">
						<div class="k">出险地点</div>
						<div class="v">{{result.area_name}}</div>
					</div>
					<div class="e-item">
						<div class="k">事件描述</div>
						<div class="v">{{result.detail}}</div>
					</div>
				</div>
			</div>
		</div>
		<div class="whiteBox">
			<div class="paddingBox">
				<div class="name">申请资料</div>
				<div class="list">
					<template v-if="1">
						<div class="li clearfix" v-for="(claim,i) in result.claim_record_data" @click="claim.url && showCoverImg(claim)">
							{{i+1 +'.'+claim.name}}
							<img src="@/assets/images/right.png" class="fr" v-if="claim.url">
							<span class="status fr" v-if="showBtn([2,4])">{{claim.status | recordStatus}}</span>
						</div>
					</template>
					<template v-else>
						<div class="l">你需要准备以下材料至离您最近的安联财险营业厅办理理赔资料提交，获取最近营业厅或咨询其他问</div>
						<div class="l">1.理赔申请书</div>
					</template>
				</div>
			</div>
		</div>
		<div class="whiteBox" v-if="showBtn([0,1,4,5,6])">
			<div class="btn fr" v-if="showBtn([5,6])" @click="toClaimApply">重新申请</div>
			<div class="btn fr" v-if="showBtn([1,4])" @click="chexiao">撤销</div>
			<div class="btn red fr" v-if="showBtn([0,4])" @click="toUpload">立即处理</div>
		</div>
		<div class="showImg" v-if="showImg.url" @click="closeImg">
			<img :src="showImg.url" :style="imgStyle($event)">
			<div class="billNumber" v-if="showImg.billNumber">
				票据业务流水号：{{showImg.billNumber}}
			</div>
		</div>
		<mPrompt :content="'确定要撤销吗？'" :btnRightText="'确定'" :btnLeftText="'取消'" @handleBtnLeft='cancel' @handleBtnRight='ok' v-if="isShow"></mPrompt>
	</div>
</template>

<script>
	import mPrompt from '@/components/base/mPrompt.vue';
	export default {
		data() {
			return {
				steps: ['发起申请', '提交资料', '审核资料','理算理赔额', '理赔完结'],
				stepLive: true,
				result: null,
				showImg:{},
				isShow: false
			}
		},
		computed: {
			stepNow(){
				let step=1;
				switch(this.result.status){
					case 0: step=2;break;
					case 1: step=3;break;
					case 2: step=4;break;
					case 3: step=5;break;
					case 4: step=2;break;
					case 6: step=5;break;
					default: console.error('step is not defined')
				}
				return step;
			},
			stepBarWidth() {
				return {
					width: (this.stepNow - 1) / (this.steps.length - 1) * 100 + '%'
				}
			}
		},
		methods: {
			showBtn(statusArr){
				return statusArr.find(status => status==this.result.status)!==undefined;
			},
			showCoverImg(claim){
				this.showImg = claim
			},
			closeImg(){
				this.showImg = {}
			},
			imgStyle(event){
				let img = event.target;
				let shape = img.naturalWidth / img.naturalHeight;
				if(shape>1){
					img.style.width = '100%';img.style.height = 'auto'
				}else{
					img.style.height = '100%';img.style.width = 'auto'
				}
			},
			//撤销
			chexiao(){
				this.isShow = true
			},
			ok(){
				this.$api.claim.claimRevoke({
					id: this.result.id
				}).then((res) => {
					console.log(res)
					this.$toast.center(res.message)
					this.isShow = false;
				})
			},
			cancel(){
				this.isShow = false
			},
			
			//很跳啊！
			toClaimApply(){
				this.$router.push('/claimList/claimApply?groupOrderSn='+this.result.order_sn)
			},
			toUpload(){
				this.$router.push('/claimUpload?claimApplySn='+this.result.sn)
			}
		},
		mounted() {
			if (!this.$route.params.id) {
				console.error('路由id没传');
				return
			}
			this.$api.claim.claimView({
					id: this.$route.params.id
				})
				.then(res => {
					console.log('console', res);
					this.result = JSON.parse(JSON.stringify(res.data))
				})
		},
		components: {
			mPrompt
		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import '@/assets/styles/mixin.scss';
	.settle {
		position: fixed;
		z-index: 100;
		top: pxToRem(84px);
		left: 0;
		bottom: 0;
		right: 0;
		background: #F5F5F5;
		overflow-y: scroll;
		.ttl {
			padding-left: pxToRem(30px);

			.bd {
				padding: pxToRem(26px) pxToRem(30px) pxToRem(26px) 0;
				border-bottom: 1px solid #F5F5F5;
			}
		}
		.step {
			.name {
				margin: pxToRem(30px) 0;
				color: #1C1D1D;
				line-height: pxToRem(36px);
				font-size: pxToRem(36px);
				padding: 0 pxToRem(30px);
			}
			.step-bar {
				position: relative;
				margin: pxToRem(66px) auto pxToRem(105px);
				width: pxToRem(576px);
				display: flex;
				justify-content: space-between;
				height: pxToRem(4px);
				background: #E6E9E9;
				.cover {
					background: #1CD7D1;
					position: absolute;
					height: pxToRem(4px);
				}
				.step-item {
					position: relative;
					width: pxToRem(18px);
					height: pxToRem(18px);
					margin-top: pxToRem(-7px);
					background: #E6E9E9;
					border-radius: 50%;
					z-index: 1;
					.tl {
						white-space: nowrap;
						position: absolute;
						top: pxToRem(58px);
						left: 50%;
						transform: translateX(-50%);
						color: #A5A8A8;
						font-size: pxToRem(26px);
						line-height: pxToRem(26px);
					}

					&.active {
						background: #1CD7D1;

						.tl {
							color: #1C1D1D;
						}
					}

					&.live {
						.tl {
							color: #1CD7D1;
						}
					}

					&.live:after {
						position: absolute;
						top: 50%;
						left: 50%;
						content: '';
						width: pxToRem(36px);
						height: pxToRem(36px);
						transform: translate3d(-50%, -50%, 0);
						background: rgba(28, 215, 209, 0.3);
						border-radius: 50%;
						z-index: -1;
					}
				}
			}
		}

		.amount {
			margin: pxToRem(30px) pxToRem(30px) pxToRem(30px);
			font-size: pxToRem(30px);
			color: #1C1D1D;
		}

		.paddingBox {
			padding: 0 pxToRem(28px) 0 pxToRem(30px);

			&:not(:last-child) {
				border: 1px solid #EDEDED;
			}

			.name {
				position: relative;
				font-size: pxToRem(28px);
				color: #1C1D1D;
				padding-left: pxToRem(36px);
				margin: pxToRem(30px) 0;

				&:after {
					display: block;
					content: '';
					position: absolute;
					height: pxToRem(4px);
					width: pxToRem(24px);
					background: #1CD7D1;
					left: 0;
					top: 50%;
					margin-top: pxToRem(-2px);
				}
			}

			.message {
				line-height: pxToRem(38px);
				font-size: pxToRem(24px);
				color: #A5A8A8;
				padding-bottom: pxToRem(20px);

				&.bd {
					border-bottom: 1px solid #EDEDED;
				}

				.k {
					float: left;
					padding-right: pxToRem(24px);
				}

				.v {
					text-align: right;
					overflow: hidden;
					min-height: pxToRem(38px);
				}
			}

			.list {
				margin-right: pxToRem(-28px);
				font-size: pxToRem(28px);
				line-height: pxToRem(48px);
				color: #1C1D1D;

				.l:last-child {
					margin-bottom: pxToRem(20px);
				}

				.li {
					border-top: 1px solid #EDEDED;
					padding: pxToRem(21px) 0;
					padding-right: pxToRem(14px);
					img {
						width: pxToRem(48px);
						height: pxToRem(48px);
					}

					.status {
						color: #A5A8A8;
					}
				}
			}
		}

		.btn {
			margin: pxToRem(20px) pxToRem(20px) pxToRem(20px) 0;
			line-height: pxToRem(58px);
			width: pxToRem(150px);
			font-size: pxToRem(24px);
			text-align: center;
			border: pxToRem(2px) solid #EDEDED;
			color: #B6BBBB;
			background: #FFFFFF;
			border-radius: pxToRem(30px);

			&.red {
				border-color: #FF4B4B;
				color: #FF4B4B;
			}
		}
		
	}

	.whiteBox {
		background: #FFFFFF;
		margin-bottom: pxToRem(20px);
		overflow: hidden;
	}
	.showImg{
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: #FFFFFF;
		z-index: 19951105;
		.billNumber{
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			text-align: center;
			white-space: nowrap;
			color: #A5A8A8;
			font-size: pxToRem(28px);
			line-height: pxToRem(50px);
			background: #FFFFFF;
		}
		img{
			position: absolute;
			top: 50%;
			width: 100%;
			transform: translateY(-50%);
		}
	}
</style>
